<template>
  <div>
    <h4 class="text-center">{{ finishPercent }}%</h4>
    <Pie :data="data" :options="options" />
  </div>
</template>

<script>
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Pie } from "vue-chartjs";

ChartJS.register(ArcElement, Tooltip, Legend);

export default {
  props: ["labels", "datas"],
  components: {
    Pie,
  },
  data() {
    return {
      data: {
        labels: this.labels,
        datasets: [
          {
            backgroundColor: [
              "#41B883",
              "#E46651",
              "#00D8FF",
              "#DD1B16",
              "#ccc",
            ],
            data: this.datas,
          },
        ],
      },
      options: {
        responsive: true,
        // maintainAspectRatio: true,
      },
    };
  },
  computed: {
    finishPercent() {
      const num1 = this.datas[0];
      let result = 0;
      if (num1 !== 0) {
        const num2 = this.datas[0] + this.datas[1];
        result = Math.round((num1 / num2) * 100);
      }
      return result;
    },
  },
};
</script>
