<template>
  <div class="card p-2" style="width: 100%; max-height: 600px">
    <h3 class="text-center">タスク達成率</h3>
    <ChartPie :labels="['達成', '未達成']" :datas="getTaskRate" />
  </div>
  <!-- <div class="card p-2">
    <h3 class="text-center">先延ばし傾向</h3>
    <ChartRadar
      :labels="['期待', '価値', '時間']"
      :data1="getbeforeProcrastinationTendency"
      :data2="getafterProcrastinationTendency"
    />
  </div> -->
</template>

<script>
import store from "@/store";
import ChartPie from "@/components/ChartPie.vue";
// import ChartRadar from "@/components/ChartRadar.vue";

export default {
  name: "App",
  components: {
    ChartPie,
    // ChartRadar,
  },
  data() {
    return {
      radar: {
        data: {
          labels: ["a", "b", "c", "d", "e", "f", "g"],
          datasets: [
            {
              label: "aaa",
              backgroundColor: "rgba(179,181,198,0.2)",
              borderColor: "rgba(179,181,198,1)",
              pointBackgroundColor: "rgba(179,181,198,1)",
              pointBorderColor: "#fff",
              pointHoverBackgroundColor: "#fff",
              pointHoverBorderColor: "rgba(179,181,198,1)",
              data: [65, 59, 90, 81, 56, 55, 40],
            },
            {
              label: "bbb",
              backgroundColor: "rgba(255,99,132,0.2)",
              borderColor: "rgba(255,99,132,1)",
              pointBackgroundColor: "rgba(255,99,132,1)",
              pointBorderColor: "#fff",
              pointHoverBackgroundColor: "#fff",
              pointHoverBorderColor: "rgba(255,99,132,1)",
              data: [28, 48, 40, 19, 96, 27, 100],
            },
          ],
        },
        options: {
          responsive: false,
          maintainAspectRatio: false,
        },
      },
    };
  },
  computed: {
    // タスク達成率の計算
    getTaskRate() {
      let finished = 0;
      let doing = 0;
      const tasks = store.getters.getTasks;
      tasks.forEach((task) => {
        if (task.finished == 1) {
          // タスクが完了していたら
          finished += task.steps.length;
        } else {
          // 完了済みステップを加算
          task.steps.forEach((step) => {
            // ステップが完了していたら
            if (step.finished == 1) {
              finished++;
            } else {
              doing++;
            }
          });
        }
      });
      // this.pie.data.datasets[0].data = [finished, doing];
      return [finished, doing];
    },
    // 各項目の点数の取得
    getbeforeProcrastinationTendency() {
      return [10, 20, 25];
    },
    getafterProcrastinationTendency() {
      return [20, 20, 30];
    },
  },
};
</script>
