<template>
  <div class="p-2">
    <ListView class="" @selectedTaskId="selectedTaskId" />
    <ScheduleView />
    <ReportView class="card" />
    <router-link
      to="/"
      :class="{
        hide: showSwipeModalFlg == false,
        show: showSwipeModalFlg == true,
      }"
      class="overlay"
      @click="closeSwipeModal"
    ></router-link>
    <div
      :class="{
        hide: showSwipeModalFlg == false,
        show: showSwipeModalFlg == true,
      }"
      class="swipe"
    >
      <router-link
        to="/create/0"
        class="btn btn-outline-primary"
        v-if="showSwipeModalFlg == false"
        @click="closeSwipeModal"
      >
        <span>タスクを追加 ▲</span>
      </router-link>
      <router-link
        class="btn btn-outline-secondary"
        v-else
        to="/"
        @click="closeSwipeModal"
      >
        <span>閉じる ×</span>
      </router-link>

      <router-view />
      <!-- <CreateToDoView class="swipeModal" task-id="editTaskId" /> -->
    </div>
  </div>
</template>

<script>
import ListView from "./views/ListView.vue";
import ScheduleView from "./views/ScheduleView.vue";
import ReportView from "./views/ReportView.vue";
// import CreateToDoView from "./views/CreateToDoView.vue";
// import { ref } from "vue";
import store from "./store";

export default {
  data() {
    return {
      editTaskId: null,
      showSwipeModalFlg: false,
    };
  },
  components: {
    ListView,
    ScheduleView,
    ReportView,
    // CreateToDoView,
  },
  computed: {
    getEditTaskId() {
      return store.getters.getEditTaskId;
    },
  },
  watch: {
    showSwipeModalFlg(status) {
      if (status == true) {
        document.querySelector("body").style.overflow = "hidden";
      } else {
        document.querySelector("body").style.overflow = "visible";
        store.commit("selectEditTaskId", 0);
      }
    },
    getEditTaskId(val) {
      if (val > 0) {
        this.showSwipeModalFlg = true;
      }
    },
  },
  created() {
    store.commit("loadTask");
  },
  methods: {
    // 編集ボタンが押された
    selectedTaskId(id) {
      this.editTaskId = id;
      this.showSwipeModalFlg = true;
    },
    // スワイプモーダルを閉じる
    closeSwipeModal() {
      this.showSwipeModalFlg = !this.showSwipeModalFlg;
      this.editTaskId = 0;
    },
  },
};
</script>

