    <div class="p-2">
      <!-- calendar -->
      <!-- <VCalendar :attributes="dates" /> -->

      <h3 class="text-center">{{ doingTasks.length }}件のタスク</h3>
      <ul class="card">
        <li v-for="(task, index) in doingTasks" :key="index" class="card m-2">
          <ul>
            <li
              :class="{ finished: task.finished == 1 }"
              class="p-2 d-flex align-items-center justify-content-between"
