<script>
import store from "@/store";
import ModalWindow from "@/components/ModalWindow.vue";
import { ref } from "vue";

export default {
  data() {
    return {
      tasks: [],
      steps: [],
      today: "",
      todayTasks: [],
      dates: ref([
        {
          // Boolean
          bar: true,
          dates: [
            new Date(2023, 10, 1),
            new Date(2023, 10, 10),
            new Date(2023, 10, 22),
          ],
          popover: {
            label: "Take Noah to basketball practice.",
          },
          isComplete: false,
        },
        {
          // String
          bar: "red",
          dates: [
            new Date(2023, 10, 4),
            new Date(2023, 10, 10),
            new Date(2023, 10, 15),
          ],
        },
        {
          // Object
          bar: {
            style: {
              backgroundColor: "brown",
            },
          },
          dates: [
            new Date(2023, 10, 12),
            new Date(2023, 10, 26),
            new Date(2023, 10, 15),
          ],
        },
      ]),
    };
  },
  components: {
    ModalWindow,
  },
  created() {
    this.tasks = store.getters.getTasks;
    this.steps = store.getters.getSteps;

    // extraction today's task
    this.today = new Date();

    this.tasks.forEach((task) => {
      if (
        new Date(task.start_date) <= this.today &&
        new Date(task.end_date) >= this.today
      ) {
        this.todayTasks.push(task);
      }
    });
  },
  computed: {
    // 全てのタスクの取得
    getAllTasks() {
      return store.getters.getTasks;
    },
    // 作業中タスクの取得
    doingTasks() {
      return this.tasks.filter((task) => !task.finished);
    },

    // 完了済みタスクの取得
    finishTasks() {
      return this.tasks.filter((task) => task.finished == 1);
    },
    // カレンダーの属性
    // attributes() {
    //   const calendarDates = this.dates;
    //   return;
    // },
  },
  watch: {
    tasks: {
      handler: function () {
        store.dispatch("changeTask", { task: this.tasks });
      },
      deep: true,
    },
    getAllTasks(val) {
      this.tasks = val;
    },
  },
  methods: {
    // 優先順位の表示
    priorityLabel(num) {
      num = Number(num);
      switch (num) {
        case 1:
          return "高";
        case 2:
          return "中";
        case 3:
          return "小";
      }
    },

    finishToDo(index) {
      this.tasks.splice(index, 1);
    },
    // 編集ボタンを押したら
    sendTaskId(id) {
      store.commit("selectEditTaskId", id);
      // this.$emit("selectedTaskId", id);
    },
    // 削除ボタンを押したら
    deleteTask(index) {
      this.tasks.splice(index, 1);
      store.dispatch("changeTask", this.tasks);
    },
  },
};
</script>

