<template>
  <div class="p-2 swipeModal">
    <h2 v-if="taskId == 0" class="m-0 pt-3 text-center">タスク作成</h2>
    <h2 v-else class="m-0 pt-3 text-center">タスク編集</h2>
    <b-form>
      <!-- タスク名入力 -->
      <b-form-group class="mt-4">
        <label for="task-name">タスク名</label>
        <BFormInput
          id="task-name"
          v-model="taskName"
          placeholder="レポート制作"
          required
        />
      </b-form-group>

      <!-- ステップ追加 -->
      <b-form-group class="mt-4" label="ステップ">
        <p>
          <small>タスクを完了するために取り組む作業を入力してください</small>
        </p>
        <b-input-group class="mb-2" v-for="(step, index) in steps" :key="index">
          <b-form-input type="text" v-model="steps[index]" />
          <b-input-group-append v-if="steps.length > 1">
            <BButton variant="outline-primary" @click="removeStep(index)"
              >削除</BButton
            >
          </b-input-group-append>
        </b-input-group>
        <BButton variant="primary" @click="addStep" class="mx-auto"
          >ステップを追加＋</BButton
        >
      </b-form-group>

      <!-- 優先順位選択 -->
      <b-form-group class="mt-4">
        <label for="task-priority">タスクの優先順位</label>

        <div class="input-group mb-3">
          <div class="input-group-prepend"></div>
          <select
            class="custom-select"
            id="task-priority"
            v-model="taskPriority"
          >
            <option value="1">高</option>
            <option value="2" selected>中</option>
            <option value="3">小</option>
          </select>
        </div>
      </b-form-group>

      <!-- 期限選択 -->
      <b-form-group class="mt-4" label="期限">
        <BFormInput
          type="date"
          class="form-control"
          v-model="deadLineDate"
          :min="today"
          @input="selectDeadlineDate()"
        />
        <!-- 時間選択 -->
        <!-- <BFormInput
          type="time"
          class="form-control"
          v-if="addTimeFlg"
          v-model="deadLineTime"
        />
        <BFormCheckbox
          id="addTimeCheck"
          class="form-check-input"
          v-model="addTimeFlg"
        >
          時間を追加
        </BFormCheckbox> -->
      </b-form-group>

      <!-- 取り組み期間選択 -->
      <b-form-group class="mt-4" label="取り組みスケジュール">
        <!-- <b-row v-if="range">
          <b-col>
            {{ startDate }}
            <BFormInput type="date" class="form-control" v-model="startDate" />
          </b-col>
          ～
          <b-col>
            {{ endDate }}

            <BFormInput type="date" class="form-control" v-model="endDate" />
          </b-col>
        </b-row> -->
        <!-- カレンダー -->
        <VDatePicker
          expanded
          mode="date"
          v-model.range="range"
          :min-date="today"
          :max-date="deadLineDate !== null ? deadLineDate : null"
        />
      </b-form-group>
    </b-form>
    <div class="hoge-margin">
      <b-button v-if="taskId == 0" block variant="primary" @click="createTask">
        <span>タスクを登録</span>
      </b-button>
      <b-button v-else block variant="primary" @click="saveTask">
        <span>変更を保存</span>
      </b-button>
    </div>
  </div>
</template>

<style>
.hoge-margin {
  margin: 50px 0;
  .btn {
    height: auto;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
</style>

<script>
// import router from "@/router";
import store from "@/store";
import { ref } from "vue";
import { formingDate } from "@/script/common.js";

export default {
  props: ["id"],
  data() {
    return {
      range: ref({}),
      today: null,
      taskId: this.id,
      taskName: "",
      steps: [""],
      taskPriority: 2,
      priorityOptions: [
        { value: "1", text: "高" },
        { value: "2", text: "中" },
        { value: "3", text: "小" },
      ],
      addTimeFlg: false,
      deadLineDate: null,
      deadLineTime: "12:00:00",
      startDate: null,
      endDate: null,
    };
  },
  created() {
    this.today = formingDate(new Date());

    // 編集モードの場合
    if (this.taskId > 0) {
      const task = store.getters.getTasks.filter(
        (task) => task.id == this.taskId
      )[0];

      // タスク名
      this.taskName = task.name;

      // ステップ
      this.steps = [];
      task.steps.forEach((step) => {
        this.steps.push(step.step);
      });

      // 期限 - 日
      this.deadLineDate = task.deadline_date;

      // 期限 - 時間
      if (task.deadline_time !== null) {
        this.addTimeFlg = true;
        this.deadLineTime = task.deadline_time;
      }
    }
  },
  watch: {
    range: {
      handler: function (val) {
        this.startDate = formingDate(val.start);
        this.endDate = formingDate(val.end);
      },
      deep: true,
    },
    // startDate(val) {
    //   const start = new Date(val);
    //   if (start !== this.range.value.start) {
    //     this.range.value.start = start;
    //   }
    // },
  },
  methods: {
    // ステップ入力フォーム追加
    addStep() {
      this.steps.push("");
    },
    // ステップ入力フォーム削除
    removeStep(index) {
      this.steps.splice(index, 1);
    },
    selectDeadlineDate() {},
    // 新しいタスクの追加
    createTask() {
      const taskJson = {
        name: this.taskName,
        deadline_date: this.deadLineDate,
        deadline_time: this.deadLineTime,
        priority: this.taskPriority,
        start_date: this.startDate,
        end_date: this.endDate,
        finished: 0,
        steps: [],
      };

      const taskStep = [];
      this.steps.forEach((step) => {
        if (step !== "") {
          taskStep.push({
            step,
            finished: 0,
          });
        }
      });

      let id = 0;
      if (store.getters.getTasks.length < 1) {
        id = 1;
      } else {
        id = store.getters.getTasks.slice(-1)[0].id + 1;
      }

      taskJson.id = id;
      taskJson.steps = taskStep;

      if (this.addTimeFlg === false) {
        taskJson.deadline_time = null;
      }

      // タスク情報を保存
      store.dispatch("addTask", { task: taskJson });

      // move list page
      // router.push("/");
    },
    saveTask() {
      // タスク情報の上書き
      const taskJson = {
        name: this.taskName,
        deadline_date: this.deadLineDate,
        deadline_time: this.deadLineTime,
        priority: this.taskPriority,
        start_date: this.startDate,
        end_date: this.endDate,
        finished: 0,
        steps: [],
      };

      const taskStep = [];
      this.steps.forEach((step) => {
        if (step !== "") {
          taskStep.push({
            step,
            finished: 0,
          });
        }
      });

      taskJson.id = this.taskId;
      taskJson.steps = taskStep;

      // 全タスク取得
      let tasks = store.getters.getTasks;

      // 新タスクの上書き
      const newTasks = [];
      tasks.forEach((task) => {
        if (task.id == this.taskId) {
          newTasks.push(taskJson);
        } else {
          newTasks.push(task);
        }
      });

      console.log(newTasks);

      // タスク情報の保存
      store.dispatch("changeTask", { task: newTasks });
      // router.push("/");
    },
  },
};
</script>

<style>
.swipeModal {
  width: 100%;
  height: calc(100vh - (5vh + 3rem));
  overflow-y: auto;
}
</style>
