import { createStore } from "vuex";
import { saveLocalstorage } from "@/script/common";

export default createStore({
  state: {
    tasks: [
      {
        name: "レポート制作（例）",
        deadline_date: "2023-11-23",
        deadline_time: null,
        priority: 2,
        start_date: "2023-11-13",
        end_date: "2023-11-19",
        finished: 0,
        steps: [
          {
            step: "テーマ選択",
            finished: 0,
          },
          {
            step: "文献調査",
            finished: 0,
          },
          {
            step: "下書き",
            finished: 0,
          },
          {
            step: "清書",
            finished: 0,
          },
          {
            step: "提出",
            finished: 0,
          },
        ],
        id: 1,
      },
    ],
    editTaskId: 0,
  },
  getters: {
    // 全タスクを取得
    getTasks(state) {
      return state.tasks;
    },
    // 編集するタスクIDを取得
    getEditTaskId(state) {
      return state.editTaskId;
    },
    // スケジュールに用いるdatasetを取得
    getDateset(state) {
      console.log(state);
    },
  },
  mutations: {
    // 編集したいタスクidの変更
    selectEditTaskId(state, id) {
      state.editTaskId = id;
    },
    // 新たなタスクの追加
    addTask(state, { task }) {
      state.tasks.push(task);
    },
    // タスクの変更
    changeTask(state, { task }) {
      state.tasks = task;
    },
    // タスクの保存
    saveTask(state) {
      saveLocalstorage("tasks", JSON.stringify(state.tasks));
    },
    // localStorageからの読み込み
    loadTask(state) {
      if (localStorage.getItem("tasks")) {
        state.tasks = JSON.parse(localStorage.getItem("tasks"));
      }
    },
  },
  actions: {
    addTask(context, { task }) {
      context.commit("addTask", { task });
      context.commit("saveTask");
    },
    changeTask(context, { task }) {
      context.commit("changeTask", { task });
      context.commit("saveTask");
    },
  },
  modules: {},
});
