<template>
  <div>
    <h3 class="text-center">スケジュール</h3>
    <VCalendar id="calendar" :attributes="attributes" />
  </div>
</template>

<script setup>
import store from "@/store";
import { ref, computed } from "vue";

const colorArr = ["red", "blue", "green", "orange", "pink"];

const dateset = computed(() => {
  const tasks = store.getters.getTasks;
  let datesets = [];
  tasks.forEach((task, index) => {
    const arr = {
      description: task.name,
      isComplete: false,
      dates: [[new Date(task.start_date), new Date(task.end_date)]],
      color: colorArr[index],
    };
    datesets.push(arr);
  });
  return datesets;
});

const dates = ref(dateset);

